.map{
  height: 41.8125em;
  width: 100%;
  .ymaps-2-1-79-balloon{
    font-size: 1em;
    font-family: $font1;
    box-shadow: none;
    padding: 0;
    max-width: calc(100vw);
    width: 16.75em;
    &__tail{
      box-shadow: none;
      background: unset;
      &:after{
        display: none;
      }
    }
    &__layout{
      box-shadow: 0 .25em 1.0625em 0 rgba(0, 0, 0, 0.21);
      border-radius: 0;
      padding: 1.56em;
    }
    &__content{
      font-family: $font1;
      font-size: 1em;
      padding: 0;
      min-width: unset;
      &>*{
        width: unset !important;
        height: unset !important;
      }
    }
    &__close-button{
      width: .625em;
      height: .625em;
      background-image: url(../img/close.svg);
      @include bgImgSize(100%);
      opacity: 1;
      margin: -.9375em -.9375em 0 0;
    }
  }
}

.place-card{
  @include grid100gap(1.56em);
  &__group{
    @include grid100gap(.5em);
  }
  &__title{
    font-size: .9375em;
    line-height: 75%;
    text-transform: uppercase;
    font-weight: 700;
    color: #AEAEAE;
  }
  &__desc{
    font-size: 1em;
    line-height: 150%;
    font-weight: 500;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    a{
      line-height: inherit;
    }
  }
}

.contacts-map{
  position: relative;
}

.contacts-form{
  position: absolute;
  top: -3.31em;
  left: 6.25em;
  width: 43.3125em;
  padding: 3.19em 2.5em;
  background: #FFF;
  box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0.06), 0px 10px 22px 0px rgba(0, 0, 0, 0.06), 0px 40px 40px 0px rgba(0, 0, 0, 0.05), 0px 91px 55px 0px rgba(0, 0, 0, 0.03), 0px 162px 65px 0px rgba(0, 0, 0, 0.01), 0px 253px 71px 0px rgba(0, 0, 0, 0.00);
  .text34{
    text-align: center;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.25em));
    gap: 2.5em;
    margin-top: 1.875em;
  }
  ._full{
    grid-column: 1/-1;
  }
  .opinion-form__nav{
    margin-top: 2.5em;
    .m-btn{
      width: 13.1875em;
    }
  }
}

.contacts-info{
  padding: 2.63em 0;
  &__grid{
    display: flex;
    justify-content: space-between;
  }
  &__col{
    max-width: 29em;
  }
  &__title{
    letter-spacing: -0.0375em;
  }
  &__codes{
    margin-top: .75em;
    color: #3D3D3D;
    .text13{
      line-height: 207%;
      letter-spacing: -0.02438em;
    }
  }
  &__address{
    margin-top: .75em;
    max-width: 18em;
    color: #3D3D3D;
    .text15{
      letter-spacing: -0.03563em;
    }
    span{
      color: #A9A9A9;
    }
  }
  &__factories{
    max-width: 23em;
  }
}

.contacts-quote{
  padding: 5.5em .94em;
  background-size: cover;
  background-position: center;
  color: #fff;
  &:after{
    background: linear-gradient(0deg, rgba(23, 23, 23, 0.64) 0%, rgba(23, 23, 23, 0.64) 100%);
  }
  &__text{
    position: relative;
    z-index: 2;
    width: 53.875em;
    max-width: 100%;
    margin: 0 auto;
    font-style: italic;
    text-align: center;
    .text23{
      line-height: 143%;
    }
  }
}

.contacts-group{
  border-bottom: 1px solid #F6F6F6;
  &__col{
    padding: 4.19em 0;
  }
  &__inner{
    display: grid;
    grid-template-columns: 27.4375em calc(100% - 27.4375em);
  }
  .text38{
    letter-spacing: -0.03em;
  }
  &._bottom{
    .contacts__items{
      background: #FBFBFB;
    }
    @media screen and (min-width: 993px){
      .contacts-group__col{
        padding-top: 1.63em;
        padding-bottom: 1.63em;
      }
    }
    .contacts__socials{
      align-items: center;
      align-self: center;
    }
  }
}

.contacts{
  &__items{
    padding-left: 4.4em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - .94em * 3 / 4));
    gap: .94em;
    border-left: 1px solid #F6F6F6;
  }
}

.contact-item{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  &__title{
    font-size: .9375em;
    line-height: 326%;
    font-weight: 700;
    color: #AEAEAE;
    text-transform: uppercase;
  }
  &__desc{
    font-size: 1.1875em;
    line-height: 147%;
    font-weight: 500;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    a{
      line-height: inherit;
    }
  }
  &__link{
    margin-top: .5em;
    font-weight: 500;
    color: $primary;
    &:hover{
      opacity: .9;
    }
    span{
      font-size: .9375em;
      line-height: 187%;
    }
  }
  .socials{
    margin-top: .375em;
    &__item{
      width: 1.875em;
      height: 1.875em;
      @include lastM(0 .56em 0 0);
    }
  }
}


@media screen and (max-width: 992px) {
  .contacts-form{
    padding: 1.37em 2.46em;
    top: -8.31em;
    left: 50%;
    transform: translateX(-50%);
    width: 41.625em;
    max-width: calc(100% - 1.875em);
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
      margin-top: 1.25em;
    }
    .opinion-form__nav{
      margin-top: 1.25em;
    }
  }

  .contacts-info{
    padding: 1.56em 0;
    &__grid{
      display: grid;
      grid-template-columns: repeat(3,calc(100% / 3 - 3.125em / 3));
      gap: 1.5625em;
    }
  }

  .contacts-quote{
    padding: 2.38em .94em 10.75em;
    &__text{
      width: 36.5em;
      .text23{
        font-size: 1.125em;
        line-height: 155%;
      }
    }
  }

  .contacts-group{
    .container{
      padding: 0;
    }
    &__inner{
      grid-template-columns: 100%;
    }
    &__col{
      padding: 1.56em;
    }
    &._bottom {
      .contacts__items {
        order: -1;
        border-bottom: 1px solid #F6F6F6;
        border-top: none;
      }
    }
    .subscribe-socials{
      flex-direction: row;
      justify-content: center;
    }
    .socials{
      margin: 0 0 0 1.5em;
    }
  }

  .contacts{
    .text38{
      font-size: 1.5em;
      line-height: 146%;
    }
    &__items{
      padding-top: 1.25em;
      padding-bottom: 1.25em;
      grid-template-columns: repeat(2,calc(50% - .47em));
      border-top: 1px solid #F6F6F6;
      border-left: none;
      align-items: flex-start;
    }
    &__socials{
      padding-top: .94em;
      padding-bottom: .94em;
    }
  }

  .contact-item{
    @include grid100gap(.44em);
    &__title{
      line-height: 140%;
    }
    .socials,
    &__link{
      margin: 0;
    }
  }
}


@media screen and (max-width: 576px){
  .map{
    height: 50.5625em;
  }

  .contacts-form{
    top: -3.19em;
    padding: 1.38em 1.19em;
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .contacts-info{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .contacts-quote{
    padding-bottom: 5.5em;
  }

  .contacts-group{
    &__col{
      padding: 1.69em .94em;
    }
    .socials{
      margin-left: .625em;
      &__item{
        @include lastM(0 .25em 0 0);
      }
    }
  }

  .contacts{
    &__items{
      padding: 1.25em .94em;
      grid-template-columns: 100%;
    }
    &__socials{
      padding: .94em;
    }
  }
}
