.cooperation-main {
  background-image: url("../img/cooperation-main__bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding-top: 9.75em;
  &__title {
    text-align: center;
    color: #ffffff;
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    background: #FFF;
    padding: 3.125em 6.25em 4.375em;
    margin-top: 9.75em;
  }
  &__phone {
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-right: 6.25em;
  }
  &__phone-title {
    color: #535353;
    padding-left: 1.875em;
  }
  &__phone-number {
    display: flex;
    align-items: center;
    i {
      flex-shrink: 0;
      width: 1.25em;
    }
    i svg {
      width: 100%;
    }
    i svg path {
      transition: all 125ms;
    }
    span {
      margin-left: 0.5em;
    }
    &:hover {
      i svg path {
        stroke: #e31e24;;
      }
    }
  }
  &__phone-info {
    color: #737373;
    padding-left: 1.875em;
  }
  &__text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 6.26em;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: -3.125em;
      left: 0;
      width: 0.0625em;
      height: calc(100% + 7.5em);
      background: #EBEBEB;
    }
    p:not(:first-child) {
      margin-top: 0.625em;
    }
  }
}

.cooperation-advantages {
  padding: 2.1875em 0 0;
  position: relative;
  z-index: 2;
  &__top {
    max-width: 43.75em;
    display: grid;
    group-reset: 100%;
    gap: 0.5em;
    text-align: center;
    margin:  0 auto;
  }
  &__wrap {
    display: grid;
    grid-template-columns: 100%;
    gap: 2.5em;
    margin-top: 2.5em;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3));
  }
  &__items {
    border: 1px solid #F0F0F0;
  }
  &__item {
    display: flex;
    justify-content: space-between;
    padding: 2.31em 3.0625em;
    &-desc {
      max-width: 21.25em;
      display: grid;
      grid-template-columns: 100%;
      gap: 0.5em;
      margin-right: 1.25em;
    }
    &-icon {
      flex-shrink: 0;
      max-width: 3.875em;
      img {
        max-width: 100%;
      }
    }
    &-number {
      flex-shrink: 0;
      margin-top: 0.3125em;
      margin-right: 0.625em;
    }
    &-text{
      line-height: 147%;
    }
  }
  &__item2 {
    display: flex;
    background: #FFF;
    padding: 2.5em 3.125em;
    &:not(:last-child) {
      border-right: 0.0625em solid #F0F0F0;
    }
  }
}

.opinion-cooperation{
  margin-top: -6em;
  margin-bottom: 3em;
  .opinion__inner{
    min-height: 50em;
    padding-top: 11em;
  }
}

.sales-field{
  margin-top: 1.875em;
  display: flex;
  align-items: center;
  justify-content: center;
  &__radios{
    display: flex;
    align-items: center;
    margin-left: 2.125em;
    .m-radio{
      @include lastM(0 2.125em 0 0);
    }
  }
}


@media screen and (max-width: 992px) {
  .cooperation-main {
    background-image: unset;
    padding-top: 0;
    .container {
      padding: 0;
    }
    &__title {
      background-image: url("../img/cooperation-main__bg.png");
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      padding: 4.8125em 0.9375em;
    }
    &__wrap {
      align-items: start;
      padding: 1.5625em;
      margin-top: 0;
    }
    &__phone {
      padding: 0;
      margin-right: 1.875em;
    }
    &__text {
      padding: 0;
      &:after {
        display: none;
      }
    }
  }

  .cooperation-advantages {
    padding: 1.875em 0 0;
    &__top {
      max-width: 35.625em;
    }
    &__wrap {
      gap: 1.875em;
      margin-top: 1.875em;
    }
    &__items2 {
      grid-template-columns: 100%;
    }
    &__item {
      flex-direction: column;
      justify-content: start;
      padding: 1.5625em;
      &-icon {
        order: -1;
        max-width: 3em;
      }
      &-desc {
        margin: 1.25em 0 0 0;
      }
    }
    &__item2 {
      padding: 0.9375em;
      &:not(:last-child) {
        border-right: unset;
        border-bottom: 0.0625em solid #F0F0F0;
      }
    }
  }

  .certificates-main {
    padding: 2.5em 0 3.75em;
    &__items {
      max-width: 100%;
      grid-template-columns: repeat(2, calc(50% - 1.5625em / 2));
      gap: 1.5625em;
      padding: 0 1.5625em;
      margin-top: 1.5625em;
    }
  }

  .opinion-cooperation{
    margin-top: -8.06em;
    margin-bottom: 1.5em;
    .opinion__inner{
      min-height: 46em;
    }
  }

  .sales-field{
    margin-top: 1.25em;
    flex-direction: column;
    &__radios{
      margin-top: .875em;
    }
    .m-radio{
      @include lastM(0 .875em 0 0);
    }
  }
}

@media screen and (max-width: 576px) {
  .text26-tablet {
    font-size: 1.625em;
  }
  ._cooperation {
    margin-bottom: 11.875em;
    position: relative;
  }
  .cooperation-main {
    &__title {
      padding: 14em 0.9375em 5em;
      margin-top: -10em;
      br {
        display: none;
      }
    }
    &__wrap {
      flex-direction: column;
      align-items: center;
      text-align: center;
      padding: 1.5625em 0.9375em;
    }
    &__phone {
      margin:  0 0 0.625em;
    }
  }
  .cooperation-advantages {
    &__items {
      grid-template-columns: 100%;
    }
    &__item {
      flex-direction: row;
      &-desc {
        margin: 0 0.9375em 0 0;
      }
      &-icon {
        order: 1;
      }
    }
  }

  .opinion-cooperation{
    margin-top: -15em;
    .opinion__inner{
      padding-top: 18em;
      min-height: 30em;
    }
  }
}
