.rating-block{
  display: flex;
  &._disabled{
    pointer-events: none;
  }
}

.rating{
  cursor: pointer;
  &__stars{
    width: 5.5em;
    max-width: 100%;
    position: relative;
    img{
      width: 100%;
      display: block;
    }
  }
  &__fill{
    width: 0;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    overflow: hidden;
    img{
      width: 5.5em;
      display: block;
    }
  }
}

.rating-input {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  position: relative;
  &--read-only {
    pointer-events: none;
  }
  &__field{
    position: absolute;
    left: 0;
    top: 100%;
    opacity: 0;
    pointer-events: none;
  }
  &__item {
    flex-shrink: 0;
    transition: all .3s;
    width: 1.8125em;
    height: 1.8125em;
    margin: 0 .15625em;
    background: #F9F9F9;
    @include center;
    svg{
      width: 1.0625em;
      height: auto;
    }
    path{
      fill: #E0E0E0;
    }
    &:hover {
      cursor: pointer;
      path{
        fill: $black;
      }
      &~.rating-input__item {
        path{
          fill: $black;
        }
      }
    }
  }
}

.rating-input[data-total-rating="1"]:not(:hover) .rating-input__item:nth-child(n+5),
.rating-input[data-total-rating="2"]:not(:hover) .rating-input__item:nth-child(n+4),
.rating-input[data-total-rating="3"]:not(:hover) .rating-input__item:nth-child(n+3),
.rating-input[data-total-rating="4"]:not(:hover) .rating-input__item:nth-child(n+2),
.rating-input[data-total-rating="5"]:not(:hover) .rating-input__item:nth-child(n+1) {
  path{
    fill: $black;
  }
}

.color-field__item{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.fg {
  position: relative;
  margin: 0;
  input {
    border: none;
    border-bottom: 1px solid #CACACA;
    padding: 0 .625em;
    height: 3.75em;
    width: 100%;
    font-size: 1em;
    background-color: transparent;
    color: $black;
    &::placeholder {
      color: #575757;
    }
    &:focus {
      &::placeholder {
        border-color: $black;
      }
    }
  }

  .nice-select{
    min-width: 100%;
    width: auto;
    font-size: 1em;
    padding: 0 1.25em;
    border: 1px solid #F3F3F3;
    height: 2.375em;
    border-radius: 0;
    display: flex;
    align-items: center;
    &:focus:not(.open){
      border-color: $black;
    }
    .current{
      width: 100%;
      color: #808080;
      font-size: 1em;
      line-height: 125%;
      padding-right: 1em;
      background-image: url(../img/arrow-down.svg);
      background-repeat: no-repeat;
      background-position: right center;
      background-size: .5em auto;
    }
    .list{
      border: 1px solid #F3F3F3;
      border-top: none;
      margin-top: 0;
      border-radius: 0;
      background-color: #fff;
      box-shadow: none;
      width: calc(100% + 2px);
      margin-left: -1px;
    }
    .option{
      font-size: 1em;
      line-height: 125%;
      height: 2.5em;
      padding: 0 1.25em;
      color: #808080;
      display: flex;
      align-items: center;
      border: none !important;
      &.selected{
        font-weight: 400 !important;
        background-color: $black;
        color: #fff;
      }
    }
    &.open{
      border-bottom: transparent;
    }
  }
  .error-text{
    margin-top: .44em;
    color: $primary;
    .text14{
      line-height: 186%;
    }
  }
  &._error{
    input{
      border-color: $primary;
      color: $primary;
      &::placeholder{
        color: $primary;
      }
    }
  }
}


.color-check,
.m-radio,
.file-input,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  label{
    position: relative;
    cursor: pointer;
    display: block;
    padding-top: .1875em;
    span{
      font-size: 1em;
      line-height: 125%;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      transition: all .100s;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
  &._error{
    input:not(:checked){
      &~label:before{
        border-color: #FF1515;
      }
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-color: $black;
        border-color: $black;
        background-size: .8125em auto;
      }
    }
  }
  label {
    padding-left: 2.375em;
    min-height: 1.625em;
    &:before {
      border: 1px solid $black;
      width: 1.625em;
      height: 1.625em;
      @include bgImgSize(0);
      background-image: url(../img/check2.svg);
      transition: 0s;
    }
  }
}


.m-radio{
  input:checked~label{
    &:before{
      border-color: $primary;
    }
    &:after{
      opacity: 1;
    }
  }
  label{
    padding-left: 2.625em;
    padding-top: .22em;
    min-height: 2.0625em;
    &:before{
      width: 2.0625em;
      height: 2.0625em;
      border: 1px solid #CCC;
      border-radius: 50%;
    }
    &:after{
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      width: 2.0625em;
      height: 2.0625em;
      border-radius: 50%;
      background-image: url(../img/radio2.svg);
      @include bgImgSize(100%);
      transition: all .1s;
      opacity: 0;
      z-index: 2;
    }
    span{
      font-size: 1.125em;
      line-height: 144%;
    }
  }
}
