.faq-page{
  padding: 3em 0 6.25em;
  &__items{
    width: 70.875em;
    max-width: 100%;
    margin: 0 auto;
  }
}

.opinion{
  background: #FBFBFB;
  &__inner{
    padding: 4.88em 0 5.7em;
    min-height: 29em;
    display: flex;
    align-items: center;
  }
}

.opinion-form{
  width: 46.25em;
  max-width: 100%;
  margin: 0 auto;
  &__top{
    text-align: center;
    @include grid100gap(1.06em);
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .9375em));
    gap: 1.875em;
    margin-top: 2.81em;
    ._full{
      grid-column: 1/-1;
    }
  }
  &__nav{
    margin-top: 1.875em;
    display: flex;
    align-items: center;
    .m-btn{
      width: 18.25em;
    }
  }
  &__hint{
    margin-left: 1.875em;
    color: #5E5E5E;
    max-width: 23em;
    .text15{
      line-height: 180%;
    }
    a{
      text-decoration: underline;
    }
  }
}

.success-form{
  text-align: center;
  max-width: 37em;
  margin: 0 auto;
  @include grid100gap(1.56em);
  &__check{
    width: 3.8125em;
    height: 3.8125em;
    margin: 0 auto;
    background: #F1F1F1;
    border-radius: 50%;
    @include center;
    img{
      width: 1.0625em;
    }
  }
  .text17{
    line-height: 153%;
  }
}


@media screen and (max-width: 992px){
  .faq-page{
    padding: 2.5em 0;
  }

  .opinion{
    &__inner{
      padding: 1.88em 0 2.94em;
      min-height: 29em;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }

  .opinion-form{
    &__top{
      gap: 0;
      max-width: 34em;
      margin: 0 auto;
    }
    &__grid{
      margin-top: 1.56em;
      grid-template-columns: repeat(2,calc(50% - .78em));
      gap: 1.56em;
    }
    &__nav{
      margin-top: 1.56em;
      flex-direction: column;
    }
    &__hint{
      margin: 1.56em 0 0;
      text-align: center;
      max-width: 100%;
      .text15{
        font-size: .875em;
        line-height: 129%;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .faq-page{
    padding-top: 0;
  }

  .opinion-form{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
