.guarantees-main {
  padding: 5.625em 0 2.8125em;
  &__items {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 7.5em / 4));
    gap: 2.5em;
  }
  &__item {
    background: #FBFBFB;
    padding: 2.5em;
    &-icon {
      max-width: 4.125em;
      height: 3.875em;
      img {
        width: 100%;
        height: 100%;
      }
    }
    &-text {
      margin-top: 2em;
    }
  }
}
.guarantees-info {
  padding: 2.8125em 0 1.25em;
  &2 {
    padding: 1.25em 0 2.8125em;
  }
  &__flex {
    display: flex;
    justify-content: space-between;
    margin-top: 1.5625em;
  }
  &__left {
    max-width: 70.9375em;
  }
  &__right {
    flex-shrink: 0;
    width: 25em;
    margin-left: 2.5em;
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 0.9375em;
    li {
      list-style: auto;
      list-style-position: inside;
    }
  }
  &__meta {
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25em;
    padding: 2.3125em 2.8125em;
    &-wrap {
      display: grid;
      grid-template-columns: 100%;
      gap: 1.25em;
    }
    &-icon {
      max-width: 2.9375em;
      img {
        max-width: 100%;
      }
    }
  }
  &__subtitle {
    margin-bottom: 0.9375em;
  }
  &__download {
    display: flex;
    align-items: center;
    &-icon {
      flex-shrink: 0;
      width: 2em;
      img {
        width: 100%;
      }
    }
    &-text {
      margin: 0 0.75em;
    }
    &-icon2 {
      flex-shrink: 0;
      width: 1.5625em;
      img {
        width: 100%;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .guarantees-main {
    padding: 2.5em 0 0.9375em;
    &__items {
      grid-template-columns: repeat(2, calc(50% - 0.9375em / 2));
      gap: 0.9375em;
    }
    &__item {
      padding: 1.5625em;

    }
  }
  .guarantees-info {
    padding: 1.25em 0;
    &2 {
      padding:  0.9375em 0 1.875em;
    }
    &__flex {
      display: block;
      margin-top: 0.9375em;
    }
    &__left {
      max-width: 100%;
    }
    &__right {
      width: 100%;
      margin: 1.875em 0 0 0;
    }
    &__meta {
      padding: 2.8125em 1.875em;
      &-wrap {
        display: flex;
        gap: unset;
      }
      &-icon {
        flex-shrink: 0;
        margin-right: 1.25em;
      }

    }
    &__download {
      padding-left: 3.5625em;
      margin-top: -1.25em;
    }
  }
}

@media screen and (max-width: 576px) {
  .guarantees-main {
    padding: 1.25em 0;
    &__items {
      grid-template-columns: 100%;
    }
  }
  .guarantees-info {
    &__meta {
     padding: 1.5625em;
      &-wrap {
        display: grid;
        grid-template-columns: 100%;
        gap: 1.25em;
      }
    }
    &__download {
      padding-left: 0;
      margin-top: 0;
    }
  }
}