.product-detail{
  padding: 1.9375em 0 4em;
  .tabs{
    padding: 0 6.125em;
  }
  &__content{
    margin-top: 2.625em;
  }
}

.tab2{
  height: 5.4375em;
  display: flex;
  align-items: center;
  border-bottom: 1px solid transparent;
  color: rgba($darkGray,.8);
  font-weight: 500;
  transition: all .3s;
  position: relative;
  @include lastM(0 4.94em 0 0);
  &._active{
    border-color: $primary;
    color: $black;
  }
  &:hover{
    color: $black;
    cursor: pointer;
  }
  &__title{
    font-size: 1.4375em;
    line-height: 117%;
  }
  &__num{
    position: absolute;
    left: 100%;
    top: 1.6em;
    font-size: .875em;
    font-weight: 400;
    line-height: 193%;
    margin-left: .5em;
    color: #BCBCBC;
  }
}

.assembly-scheme{
  display: flex;
  align-items: center;
  color: $primary;
  &:hover{
    opacity: .8;
  }
  &__icon{
    width: 2em;
    flex-shrink: 0;
    margin-right: .75em;
  }
  &__download{
    width: 1.5625em;
    flex-shrink: 0;
    margin-left: .625em;
  }
}

.props{
  display: grid;
  grid-template-columns: calc(100% - 34.1875em - 11.625em) 34.1875em;
  gap: 1.31em 11.625em;
  &__items{
    margin-top: 3em;
    @include grid100gap(1.75em);
  }
  &__text{
    li,
    p{
      font-size: 1.125em;
      line-height: 161%;
      @include lastM(0 0 .72em);
    }
    img{
      width: 100%;
      display: block;
      margin: 2.06em 0;
      &:last-child{
        margin-bottom: 0;
      }
    }
    ul{
      @include lastM(0 0 .81em);
      li{
        position: relative;
        padding-left: 2.44em;
        &:before{
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 1.67em;
          height: 1.67em;
          background-image: url(../img/check3.svg);
          @include bgImgSize(100%);
        }
      }
    }
  }
}

.props-sidebar{
  position: sticky;
  top: 1em;
  &__title{
    font-weight: 500;
    color: rgba($darkGray,.8);
  }
}

.product-reviews{
  display: grid;
  grid-template-columns: calc(100% - 50.625em - 3.125em) 50.625em;
  gap: 3.75em 3.125em;
  &__items{
    @include grid100gap(2.5em);
  }
}

.review-card{
  @include grid100gap(.375em);
  &__date{
    color: #9D9D9D;
    line-height: 192%;
    letter-spacing: 0.00813em;
  }
  &__text{
    line-height: 156%;
  }
  &__images{
    padding: .625em 0;
    display: grid;
    grid-template-columns: repeat(5,calc(20% - 3.75em / 4));
    gap: .9375em;
  }
  &__img{
    display: block;
    @include imgCover(75%);
  }
}

.review-answer{
  margin: 2.125em 0 0 1.81em;
  padding-left: .94em;
  border-left: 2px solid $primary;
  @include grid100gap(.375em);
  &__type{
    color: $primary;
    line-height: 178%;
  }
  .review-card__text,
  &__name{
    font-style: Italic;
  }
}

.review-stat{
  margin-bottom: 3.25em;
  display: grid;
  grid-template-columns: 6.5em 20em calc(100% - 30.5em);
  gap: 2em;
  align-items: flex-start;
  &__count{
    margin-top: .625em;
    color: #505054;
  }
  &__types{
    @include grid100gap(1em);
  }
  &__text{
    color: #808080;
    .text14{
      line-height: 143%;
    }
  }
}

.review-type{
  display: grid;
  align-items: center;
  grid-template-columns: 5.1875em 10.75em calc(100% - 17.9375em);
  gap: 1em;
  &__stars{
    img{
      height: .864em;
      display: block;
      margin-left: auto;
    }
  }
  &__count{
    font-weight: 500;
    &._gray{
      color: #A8A8A8;
    }
  }
}

.review-progress{
  height: .3125em;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #EBEDED;
  }
  &__rect{
    position: absolute;
    z-index: 3;
    top: 0;
    width: .1875em;
    height: 100%;
    left: 0;
    background-color: #fff;
  }
  &__rect1{
    left: calc(20% - .75em / 4);
  }
  &__rect2{
    left: calc(40% - .75em / 4);
  }
  &__rect3{
    left: calc(60% - .75em / 4);
  }
  &__rect4{
    left: calc(80% - .75em / 4);
  }
  &__fill{
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    z-index: 2;
    background-color: #4C4C4C;
  }
}

.review-auth,
.review-thank,
.review-form{
  border: .625em solid #F9F9F9;
  padding: 2.5em 3.125em;
  position: sticky;
  top: 1em;
}

.review-form{
  &__title{
    text-align: center;
  }
  &__grid{
    margin-top: 2.37em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.25em));
    gap: 2.38em 2.5em;
  }
  ._full{
    grid-column: 1/-1;
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 2.38em;
    .m-btn{
      min-width: 14em;
      height: 3.875em;
    }
  }
  &__bottom{
    display: grid;
    grid-template-columns: 50% 50%;
    border: 1px solid #F6F6F6;
    margin-top: 2.38em;
  }
  &__rating{
    border-right: 1px solid #F6F6F6;
    padding: 1.12em;
    display: flex;
    flex-direction: column;
    align-items: center;
    .rating-field{
      margin-top: .31em;
    }
  }
}

.file-input{
  &._uploaded{

  }
  input:hover{
    &~.file-input__wrap{
      .file-input__btn{
        border-color: $primary;
      }
    }
  }
  &__wrap{
    padding: 1.19em;
    position: relative;
    min-height: 5.75em;
  }
  &__btn{
    margin-top: .31em;
    border: 1px dashed #E8E8E8;
    background: #F9F9F9;
    display: flex;
    align-items: center;
    padding: 0 .75em;
    color: $darkGray;
    height: 1.8125em;
    span{
      font-size: .9375em;
    }
    img{
      width: .8125em;
      flex-shrink: 0;
      margin-right: .69em;
    }
  }
  &__inner{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: inherit;
    width: 100%;
    position: absolute;
    @include centerAbsolute;
    z-index: -1;
    &>img{
      width: 1.125em;
      display: block;
      margin: 0 auto .44em;
    }
    .text16{
      color: #ADB1B7;
      text-align: center;
      line-height: 140%;
    }
  }
  &__upload{
    display: flex;
    flex-wrap: wrap;
    background-color: #fff;
    min-height: 100%;
    margin-top: -1.25em;
  }
}

.upload-file{
  position: relative;
  z-index: 3;
  width: 3.75em;
  margin: 1.25em 1.25em 0 0;
  &__remove{
    position: absolute;
    top: -.31em;
    right: -.31em;
    width: .8125em;
    &:hover{
      circle{
        opacity: 1;
      }
    }
    svg{
      width: 100%;
      height: auto;
    }
  }
  &__img{
    @include imgCover(100%);
  }
}

.review-thank,
.review-auth{
  &__nav{
    display: flex;
    .m-btn{
      height: 3.4375em;
      span{
        font-size: 1.0625em;
      }
    }
  }
}

.review-thank{
  text-align: center;
  &__text{
    margin: 1em auto 0;
    color: #545454;
    max-width: 35em;
  }
  &__nav{
    justify-content: center;
    margin-top: 1.81em;
  }
}

.review-auth{
  @include grid100gap(1em);
  &__text{
    color: #545454;
  }
  &__nav{
    .m-btn{
      ._view2{
        display: none;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .product-detail{
    .tabs{
      padding: 0 1.56em;
    }
    &__content{
      margin-top: 1.91em;
    }
  }

  .tab2{
    @include lastM(0 2.06em 0 0);
    &__title{
      font-size: 1.125em;
    }
  }

  .assembly-scheme{
    margin-left: 1.5em;
  }

  .props{
    grid-template-columns: 100%;
    &__text{
      p,li{
        font-size: .9375em;
        line-height: 193%;
        @include lastM(0 0 1em);
      }
      li{
        @include lastM(0 0 .73em);
      }
      img{
        margin: .94em 0;
      }
      ul{
        @include lastM(0 0 .94em);
      }
    }
    &__items{
      margin-top: .625em;
      gap: .81em;
    }
  }

  .product-reviews{
    grid-template-columns: 100%;
  }

  .review-card{
    &__images{
      grid-template-columns: repeat(4,calc(25% - 2.8125em / 3));
    }
  }

  .review-answer{
    margin-left: 0;
  }

  .review-stat{
    grid-template-columns: 6em calc(100% - 6.5em);
    gap: .5em;
    max-width: 21.5em;
    margin-bottom: 1.5em;
    &__text{
      display: none;
    }
    &__ball{
      .text46{
        font-size: 2em;
      }
      .text29{
        font-size: 1.0625em;
      }
    }
    &__types{
      gap: .5em;
    }
  }

  .review-type{
    grid-template-columns: 5em 6.75em calc(100% - 12.25em);
    gap: .5em;
    &__stars{
      img{
        height: .676em;
      }
    }
    &__count{
      .text15{
        font-size: .8125em;
      }
    }
  }

  .review-progress{
    height: .1875em;
    &__rect{
      width: .125em;
    }
    &__rect1{
      left: calc(20% - .5em / 4);
    }
    &__rect2{
      left: calc(40% - .5em / 4);
    }
    &__rect3{
      left: calc(60% - .5em / 4);
    }
    &__rect4{
      left: calc(80% - .5em / 4);
    }
  }

  .review-auth,
  .review-thank,
  .review-form{
    padding: .9375em .625em;
  }

  .review-form{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .47em));
      gap: .94em;
      margin-top: .94em;
    }
    &__nav,
    &__bottom{
      margin-top: .94em;
    }
    &__title{
      font-size: 1.6875em;
    }
  }

  .review-thank{
    &__text,
    &__nav{
      margin-top: .94em;
    }
  }

  .review-auth{
    gap: .94em;
    &__nav{
      .m-btn{
        width: 100%;
        padding: 0 .5em;
        ._view1{
          display: none;
        }
        ._view2{
          display: block;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .review-card{
    &__images{
      grid-template-columns: repeat(2,calc(50% - .9375em / 2));
    }
  }

  .review-form{
    &__grid{
      grid-template-columns: 100%;
    }
    &__bottom{
      grid-template-columns: 100%;
      border: none;
    }
    &__rating{
      border: none;
    }
  }
}
