.certificates-main {
  padding: 7.5em 0;
  &__title {
    text-align: center;
  }
  &__items {
    max-width: 59.0625em;
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 2.8125em / 2));
    gap: 2.8125em;
    margin: 3.75em auto 0;
  }
  &__item {
    background: #FFF;
    border: 1px solid #F1F1F1;
    padding: 0.9375em;
    img {
      width: 100%;
    }
  }
}

@media screen and (max-width: 992px) {
  .certificates-main {
    padding: 2.5em 0 3.75em;
    &__items {
      max-width: 100%;
      grid-template-columns: repeat(2, calc(50% - 1.5625em / 2));
      gap: 1.5625em;
      padding: 0 1.5625em;
      margin-top: 1.5625em;
    }
  }
}

@media screen and (max-width: 576px) {
  ._certificates {
    margin-bottom: 11.875em;
    position: relative;
  }
  .certificates-main {
    padding: 1.875em 0;
    &__items {
      max-width: 100%;
      grid-template-columns: 100%;
      gap: 1.5625em;
      padding: 0;
    }
  }
}